@import url('https://fonts.googleapis.com/css2?family=Spartan:wght@400;700&display=swap');

.container {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  padding-right: 15px;
  padding-left: 15px;
}
@media (min-width: 476px) {
  .container {
    padding-right: 15px;
    padding-left: 15px;
  }
}
@media (min-width: 768px) {
  .container {
    padding-right: 15px;
    padding-left: 15px;
  }
}
@media (min-width: 992px) {
  .container {
    padding-right: 15px;
    padding-left: 15px;
  }
}
@media (min-width: 1200px) {
  .container {
    padding-right: 15px;
    padding-left: 15px;
  }
}
@media (min-width: 476px) {
  .container {
    width: 100%;
  }
}
@media (min-width: 768px) {
  .container {
    width: 720px;
    max-width: 100%;
  }
}
@media (min-width: 992px) {
  .container {
    width: 960px;
    max-width: 100%;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1140px;
    max-width: 100%;
  }
}
@media (min-width: 1400px) {
  .container {
    width: 1340px;
    max-width: 100%;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Spartan', sans-serif;
  font-weight: 700;
  color: #454545;
}

h1 {
  font-size: 32px;
}

h2 {
  font-size: 28px;
}

p {
  font-family: 'Spartan', sans-serif;
  line-height: 1.5;
  font-size: 16px;
  font-weight: 600;
}

.ant-typography {
  font-family: 'Spartan', sans-serif;
  line-height: 1.5;
}

.body {
  background-color: transparent;
}
